<script>
export default {
  name: "my",
  data() {
    return {
      mycar: "",
      username: "点击登录",
      userID: "",
      Asfe: true
    };
  },
  computed: {},
  methods: {
       //去登录
    Register(o) {
      //如果读取不到用户的信息,说明没有登录,此时跳转到登录界面
      //获取用户登录状态
      if (this.user) {
        return;
      }
      this.$router.push(o);
    },
     Quit() {  
     if(this.mycar){
        localStorage.removeItem("user");       
        this.user = '';
        this.$router.push({name:'register'});
          this.$toast({
                message: "退出登录成功"
              });
          return
     }
             this.$toast({
                message: "检测不到登录状态,请去登录"
              });
    },
  },
  components: {},
  created() {
    //获取用户账号信息
    let user = JSON.parse(localStorage.getItem("user"));
    let users = JSON.parse(localStorage.getItem("users"));
    this.mycar = user;
    //判断用户是否存在
    if (user) {
      for (let i = 0; i < users.length; i++) {
        if (user.username == users[i].username) {
          this.username = users[i].username;
          return      
        }
        this.username = "点击登录";
      }
    }
  }
};
</script>